/**********************************/
/* Story Images / Imagebox and Caption Fix */
/* CONTAINER */
.vis_container, #story-content-edit figure {
  float:right;
  max-width: 45%;
	border-style: solid;
	border-width: 1px;
	border-color: #d8d8d8;
	margin-bottom: 1.11111rem;
	margin-left: 1.11111rem;
	margin-right: 0;
	padding: 1.11111rem;
	color: #333333;
}
/* IMAGES */
.vis_container .visbox img, #story-content-edit figure img {
	margin: 0px;
	padding: 0px;
	border: 1px solid #999;
	float: none;
}
@media screen and (max-width: 40em) {
  .vis_container, #story-content-edit figure {
    margin-left: .5rem;
    margin-right: 1rem;
    max-width: 100%;
    clear:both;
    float:left !important;
  }
}
#story-content-edit p img {
	margin: 1rem;
}
/* CAPTIONS */
.viscaption {
  padding: 0;
  margin: 0;
}
.viscaption p, #story-content-edit figure figcaption {
	margin: 0px;
	padding: .7rem 0px 0px 0px;
	font-size: 12px !important;
	color: #333;
}
/* Story Caption Fix */

/* Foundation fix */
.off-canvas-content, .off-canvas-content {
    padding-bottom: 0px;
}
